		heartClick(list,index){
			if(list.color==='#8F8F94'){
				list.color="#ff0000"
				this.animation = uni.createAnimation()
				this.animation.translateY(-30).opacity(1).step({
					duration:400,
					timingFunction:'ease'
				})
				list.animation = this.animation.export()
				//动画执行后撤销执行
				setTimeout(function(){
					this.animation.translateY(0).opacity(0).step({
						duration:0
					})
					list.animation = this.animation.export()
				}.bind(this),500)
			}else{
